window.React = require('react')

var Box = React.createClass({displayName: 'Box',
	close: function () {
		this.props.close();
	},
	componentDidMount: function () {
		var self = this;
		$('body').on('keypress', function(e){
			if (e.which == 27){
				self.close();
			}
		});
	},
	render: function () {
		return (
			React.createElement("div", null, 
				React.createElement("div", {className: "dialog-blackout", onClick: this.close, 'data-action': "close-dialog"}), 
				React.createElement("div", {className: "dialog-box"}, 
					React.createElement("i", {className: "close-btn", onClick: this.close, 'data-action': "close-dialog"}), 
					this.props.children
				)
			)
		);
	}
});

var Dialog = module.exports = function (component, className, onRender, onClose) {
	var $el = $('<div class="dialog">').appendTo("body");
	if (className) {
		$el.addClass(className);
	}
	function close () {
		$el.fadeOut();
		React.unmountComponentAtNode($el[0]);
		onClose && onClose($el[0], c);
		// $('html').removeClass('crop');
	}
	component.props.close = close;
	// $('html').addClass('crop');
	var c = React.render(React.createElement(Box, {close: close}, component), $el[0],
		function () {
			// Defer execution, so variable c is set.
			setTimeout(function () {
				$el.fadeIn();
				onRender && onRender($el[0], c);
				$('body').focus();
			}, 10);
		});
}

